import React, { useState } from 'react';
import {
  Container,
  Grid,
  Typography,
  Box,
  Paper,
  Tabs,
  Tab,
  TextField,
  Button,
  Avatar,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Divider,
  Card,
  CardContent,
  Switch,
  FormControlLabel,
} from '@mui/material';
import {
  Edit,
  Save,
  Delete,
  Visibility,
  VisibilityOff,
  Person,
  Security,
  Notifications,
  CreditCard,
  LocalShipping,
} from '@mui/icons-material';

const ProfileInfo = () => {
  const [isEditing, setIsEditing] = useState(false);
  const [profileData, setProfileData] = useState({
    firstName: 'John',
    lastName: 'Doe',
    email: 'john.doe@example.com',
    phone: '(555) 123-4567',
  });

  const handleSave = () => {
    setIsEditing(false);
    // In a real app, save changes to backend
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 4 }}>
        <Avatar
          sx={{
            width: 100,
            height: 100,
            bgcolor: 'primary.main',
            fontSize: '2.5rem',
            mr: 3,
          }}
        >
          {profileData.firstName[0]}
        </Avatar>
        <Box>
          <Typography variant="h5">
            {profileData.firstName} {profileData.lastName}
          </Typography>
          <Typography color="text.secondary">Member since January 2024</Typography>
        </Box>
      </Box>

      <Grid container spacing={3}>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            label="First Name"
            value={profileData.firstName}
            onChange={(e) => setProfileData({ ...profileData, firstName: e.target.value })}
            disabled={!isEditing}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            label="Last Name"
            value={profileData.lastName}
            onChange={(e) => setProfileData({ ...profileData, lastName: e.target.value })}
            disabled={!isEditing}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            label="Email"
            value={profileData.email}
            onChange={(e) => setProfileData({ ...profileData, email: e.target.value })}
            disabled={!isEditing}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            label="Phone"
            value={profileData.phone}
            onChange={(e) => setProfileData({ ...profileData, phone: e.target.value })}
            disabled={!isEditing}
          />
        </Grid>
      </Grid>

      <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
        {isEditing ? (
          <Button
            variant="contained"
            startIcon={<Save />}
            onClick={handleSave}
          >
            Save Changes
          </Button>
        ) : (
          <Button
            variant="contained"
            startIcon={<Edit />}
            onClick={() => setIsEditing(true)}
          >
            Edit Profile
          </Button>
        )}
      </Box>
    </Box>
  );
};

const SecuritySettings = () => {
  const [showPassword, setShowPassword] = useState(false);
  const [password, setPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  return (
    <Box>
      <Typography variant="h6" gutterBottom>
        Change Password
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <TextField
            fullWidth
            label="Current Password"
            type={showPassword ? 'text' : 'password'}
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            InputProps={{
              endAdornment: (
                <IconButton onClick={() => setShowPassword(!showPassword)}>
                  {showPassword ? <VisibilityOff /> : <Visibility />}
                </IconButton>
              ),
            }}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            label="New Password"
            type={showPassword ? 'text' : 'password'}
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            label="Confirm New Password"
            type={showPassword ? 'text' : 'password'}
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
          />
        </Grid>
      </Grid>
      <Button
        variant="contained"
        sx={{ mt: 3 }}
        startIcon={<Security />}
      >
        Update Password
      </Button>

      <Box sx={{ mt: 4 }}>
        <Typography variant="h6" gutterBottom>
          Two-Factor Authentication
        </Typography>
        <FormControlLabel
          control={<Switch />}
          label="Enable two-factor authentication"
        />
      </Box>
    </Box>
  );
};

const NotificationSettings = () => {
  const [notifications, setNotifications] = useState({
    orderUpdates: true,
    promotions: true,
    newsletter: false,
    productUpdates: true,
  });

  const handleChange = (name) => (event) => {
    setNotifications({ ...notifications, [name]: event.target.checked });
  };

  return (
    <Box>
      <List>
        <ListItem>
          <ListItemText
            primary="Order Updates"
            secondary="Receive notifications about your order status"
          />
          <ListItemSecondaryAction>
            <Switch
              edge="end"
              checked={notifications.orderUpdates}
              onChange={handleChange('orderUpdates')}
            />
          </ListItemSecondaryAction>
        </ListItem>
        <Divider />
        <ListItem>
          <ListItemText
            primary="Promotions"
            secondary="Receive notifications about sales and special offers"
          />
          <ListItemSecondaryAction>
            <Switch
              edge="end"
              checked={notifications.promotions}
              onChange={handleChange('promotions')}
            />
          </ListItemSecondaryAction>
        </ListItem>
        <Divider />
        <ListItem>
          <ListItemText
            primary="Newsletter"
            secondary="Subscribe to our monthly newsletter"
          />
          <ListItemSecondaryAction>
            <Switch
              edge="end"
              checked={notifications.newsletter}
              onChange={handleChange('newsletter')}
            />
          </ListItemSecondaryAction>
        </ListItem>
        <Divider />
        <ListItem>
          <ListItemText
            primary="Product Updates"
            secondary="Receive notifications about new products and restocks"
          />
          <ListItemSecondaryAction>
            <Switch
              edge="end"
              checked={notifications.productUpdates}
              onChange={handleChange('productUpdates')}
            />
          </ListItemSecondaryAction>
        </ListItem>
      </List>
    </Box>
  );
};

const PaymentMethods = () => {
  const cards = [
    {
      id: 1,
      type: 'Visa',
      last4: '4242',
      expiry: '12/24',
    },
    {
      id: 2,
      type: 'Mastercard',
      last4: '8888',
      expiry: '06/25',
    },
  ];

  return (
    <Box>
      <Button
        variant="contained"
        startIcon={<CreditCard />}
        sx={{ mb: 3 }}
      >
        Add New Card
      </Button>

      <Grid container spacing={2}>
        {cards.map((card) => (
          <Grid item xs={12} sm={6} key={card.id}>
            <Card>
              <CardContent>
                <Typography variant="h6">
                  {card.type} •••• {card.last4}
                </Typography>
                <Typography color="text.secondary">
                  Expires {card.expiry}
                </Typography>
                <Box sx={{ mt: 2, display: 'flex', gap: 1 }}>
                  <Button
                    size="small"
                    startIcon={<Edit />}
                  >
                    Edit
                  </Button>
                  <Button
                    size="small"
                    color="error"
                    startIcon={<Delete />}
                  >
                    Remove
                  </Button>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

const ShippingAddresses = () => {
  const addresses = [
    {
      id: 1,
      name: 'Home',
      address: '123 Main St',
      city: 'New York',
      state: 'NY',
      zip: '10001',
    },
    {
      id: 2,
      name: 'Office',
      address: '456 Business Ave',
      city: 'New York',
      state: 'NY',
      zip: '10002',
    },
  ];

  return (
    <Box>
      <Button
        variant="contained"
        startIcon={<LocalShipping />}
        sx={{ mb: 3 }}
      >
        Add New Address
      </Button>

      <Grid container spacing={2}>
        {addresses.map((address) => (
          <Grid item xs={12} sm={6} key={address.id}>
            <Card>
              <CardContent>
                <Typography variant="h6">{address.name}</Typography>
                <Typography color="text.secondary">
                  {address.address}
                </Typography>
                <Typography color="text.secondary">
                  {address.city}, {address.state} {address.zip}
                </Typography>
                <Box sx={{ mt: 2, display: 'flex', gap: 1 }}>
                  <Button
                    size="small"
                    startIcon={<Edit />}
                  >
                    Edit
                  </Button>
                  <Button
                    size="small"
                    color="error"
                    startIcon={<Delete />}
                  >
                    Remove
                  </Button>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

const Profile = () => {
  const [tabValue, setTabValue] = useState(0);

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const tabs = [
    { label: 'Profile', icon: <Person />, component: <ProfileInfo /> },
    { label: 'Security', icon: <Security />, component: <SecuritySettings /> },
    { label: 'Notifications', icon: <Notifications />, component: <NotificationSettings /> },
    { label: 'Payment Methods', icon: <CreditCard />, component: <PaymentMethods /> },
    { label: 'Shipping', icon: <LocalShipping />, component: <ShippingAddresses /> },
  ];

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Paper sx={{ p: 3 }}>
        <Typography variant="h4" gutterBottom>
          Account Settings
        </Typography>
        
        <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
          <Tabs
            value={tabValue}
            onChange={handleTabChange}
            variant="scrollable"
            scrollButtons="auto"
          >
            {tabs.map((tab, index) => (
              <Tab
                key={index}
                label={tab.label}
                icon={tab.icon}
                iconPosition="start"
              />
            ))}
          </Tabs>
        </Box>

        {tabs[tabValue].component}
      </Paper>
    </Container>
  );
};

export default Profile;
